import React, {Component} from 'react';
import {Container, Segment, List,Icon} from 'semantic-ui-react';
import '.././styles.css';

class Research extends Component{
    render(){
        return (
            <Segment className='padbelow' inverted basic>
                <Container className="whoop">
                    <h1>Research</h1>
                    <p className='newssize'>
                        <List>
                            <IconList src={require('../Documents/AttackoftheHollywoodStoryMachine.docx')}>
                                Attack of the Hollywood Story Machine
                            </IconList>
                            <IconList src={require('../Documents/FullCage.docx')}>
                                Full Cage
                            </IconList>
                            <IconList src={require('../Documents/BermackFemPhilFinal.pages')}>
                                Feminist Philosophy Final Paper
                            </IconList>
                        </List>
                    </p>
                </Container>
            </Segment>

        );
    }
}

function IconList(props){
    return(
            <List.Item>
                <List.Icon name='file alternate'/>
                <List.Content className='ui inl'>
                    {<a href={props.src} download>
                        {props.children}
                    </a>}
                </List.Content>
            </List.Item>
    );
}

export default Research;