import React, {Component} from 'react';
import {Container, Header, Grid, Image, Segment, List, Embed} from 'semantic-ui-react';
import '.././styles.css';
import Divider from "semantic-ui-react/dist/commonjs/elements/Divider";

class Films extends Component{
    render(){
        return (
            <Segment inverted basic>
                <Container className="whoop">
                    <Header as='h1'
                            style = {{fontSize:'4em'}}
                            className='ui color1'
                            content='Films'
                            textAlign='right'
                    />
                    <Film title={'China White (2019)'}
                          subtitle="Writer/Director/Cinematographer/Editor â€“ Zack Bermack"
                          src={require('../Images/PosterLaurelsIMDb.jpg')}
                          size='large'
                          vimid='335752530'

                          source='vimeo'
                    >
                        When The Jackal is ripped off by a flashy new kid,
                        he spirals into a bloody dance of revenge to regain his pride.
                        <br/><br/>
                        <u><i>Festivals:</i></u>
                        <List>
                            <List.Item>Alternative Film Festival. Toronto, CAN. <i>Semifinalist</i>. 2019</List.Item>
                            <List.Item>Eurasia International Monthly Film Festival, <i>Semifinalist</i>. 2019</List.Item>
                            <List.Item>Los Angeles CineFest. Los Angeles, CA. <i>Semifinalist</i>. 2019</List.Item>
                            <List.Item>New York Cinematography Awards, New York, NY. <i>Semifinalist</i>. 2019</List.Item>
                            <List.Item>Mystic Film Festival. Mystic, CT. <i>Official Selection</i>. 2019</List.Item>
                            <List.Item>First Time Filmmaker Sessions. Pinewood Studios, UK.</List.Item>
                        </List>
                    </Film>
                </Container>
            </Segment>

        );
    }
}

class Film extends Component{
    render(){
        return(
            <Container>
                <Header className='ui color1' as='h2' textAlign='center'>
                    {this.props.title}
                    <Header className='ui color1' as ='h3' content={this.props.subtitle}/>
                    <Embed
                        id={this.props.vimid}
                        placeholder={this.props.placeholder}
                        source={this.props.source}
                    />
                </Header>
                <Grid centered verticalAlign='middle'>
                    <Grid.Row>
                        <Grid.Column width={8}>
                            <p className='newssize'>
                                {this.props.children}
                            </p>
                        </Grid.Column>
                        <Grid.Column width={8} >
                            <Image className='ui shadowimage' src={this.props.src} size={this.props.size} centered/>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
                <Divider/>
            </Container>
        );
    }
}

export default Films;