import React, {Component} from 'react';
import 'semantic-ui-css/semantic.min.css';
import {Container, Menu} from 'semantic-ui-react';
import {Link} from 'react-router-dom';
import '.././styles.css';

class TopMenu extends Component {
	render(){

		const activeItem = this.props.active;
		return (
					<Menu  fluid widths={5} size='huge' fixed='top' inverted className="ui color1 menu">
						<Container>
							<Menu.Item as={Link}
							  to={'/'}
							  name='home'
					          active={activeItem === 'home'}
					          onClick={this.props.handleMenuClick}
					        />
					        <Menu.Item as={Link}
							  to={"/news"}
					          name='news'
					          active={activeItem === 'news'}
							  onClick={this.props.handleMenuClick}
					        />
					        <Menu.Item as={Link}
							  to={"/films"}
					          name='films'
					          active={activeItem === 'films'}
					          onClick={this.props.handleMenuClick}
					        />
							<Menu.Item as={Link}
								to={"/resume"}
								name='resume'
								active={activeItem === 'resume'}
								onClick={this.props.handleMenuClick}
							/>
					        <Menu.Item as={Link}
							  to={"/research"}
							  name='Research and Conferences'
					          active={activeItem === 'Research and Conferences'}
					          onClick={this.props.handleMenuClick}
					        />
						</Container>
					</Menu>
			);
	}
}

export default TopMenu;