import React, {Component} from 'react';
import 'semantic-ui-css/semantic.min.css';
import {Container, Header, Grid, Image, Divider, Segment} from 'semantic-ui-react';
import '.././styles.css';

class Homepage extends Component{
	render(){
		return (
			<Segment inverted basic>
				<Container className="whoop">
					<Container  textAlign='right'>
						<Header as='h1'
								className='ui color1'
								style = {{fontSize:'4em'}}>
							Zack Bermack
							<Header as='h2'
									className='ui color1'
									content='Filmmaker'
							/>
						</Header>
					</Container>
					<Container className='parbreak'>
						<Grid container stackable columns='equal' verticalAlign='middle'>
							<Grid.Row>
								<Grid.Column width={8}>
									<p className="mainsize">
										Zack Bermack is a writer, director, editor, cinematographer,
										and gaffer. Zack majored in Film Studies and Philosophy at Connecticut College,
										mainly focusing on film production. Zackâ€™s films have been nominated
										for numerous awards and accepted to a wide range of film festivals.
									</p>
								</Grid.Column>
								<Grid.Column width={8}>
									<Image className='ui shadowimage' centered bordered src={require('../Images/pensivezack.JPG')} size='large'/>
								</Grid.Column>
							</Grid.Row>
							<Grid.Row>
								<Grid.Column width={8}>
									<Image className='ui shadowimage' centered bordered src={require('../Images/zacklight.jpg')} size='large'/>
								</Grid.Column>
								<Grid.Column width={8}>
									<p className="mainsize">
										Zackâ€™s latest film, China White, was accepted into six film festivals
										and was named a semifinalist in four including the New York Cinematography
										Awards and the Los Angeles CineFest.
									</p>
								</Grid.Column>

							</Grid.Row>
						</Grid>
						<Grid>
							<Grid.Row columns={1}>
								<Grid.Column textAlign='center'>
									<p className="mainsize">
										Zack has been making films since early childhood, starting out making
										stop-motion animation action films with Lego figures at the age of 9.
										Critically ignored, these films have been lost for upwards of 15 years.
										Today, Zack is based in New Jersey and is doing whatever he can to be
										a part of the industry that he loves so much.
									</p>
								</Grid.Column>
							</Grid.Row>
							<Grid.Row columns={1}>
								<Grid.Column >
									<Image className='ui shadowimage' centered size='huge' src={require('../Images/zackjackpress.JPG')} />
								</Grid.Column>
							</Grid.Row>
						</Grid>
					</Container>
				</Container>
			</Segment>


			);
	}
}

export default Homepage;