import React, {Component} from 'react';
import {Container,Header,Segment} from 'semantic-ui-react';
import '.././styles.css';

class News extends Component{
    render(){
        return (
            <Segment inverted basic>
                <Container className="whoop">
                    <Story title='Not Cancelled, Just Postponed' date='March 14, 2020'>
                        After having been in discussion with our professors and the higher-ups at Connecticut College
                        and gathering all of the information we could find, we have decided that we must postpone the
                        production of High Heat. We have decided that it would be irresponsible for us to gather so many
                        people from around the country to shoot at this point in time, as we are all very terrified
                        of COVID-19. In the next day or so, the Kickstarter campaign will be canceled.
                        <br/><br/>
                        We are still going to make this movie, but we are going to have to wait until everything blows
                        over. We may postpone until the Summer or even later. We would still absolutely love for you to
                        be involved in the project and have been truly thankful for your enthusiasm. When the time comes
                        for the project to get back into gear, we would love to reach back out to you and see if you are
                        still available and interested in supporting High Heat.
                        <br/><br/>
                        This update is devastating for us, as we have been working tirelessly all semester on this film,
                        but we cannot ethically shoot this project in the current state of the country.
                        <br/><br/>
                        We hope that you all understand, and we hope that when the time comes again we will have your support.
                        <br/><br/>
                        Thank you so much, and please stay safe and stay healthy.

                    </Story>
                    <Story title="We're crushing our goals! Amazing!" date='March 7, 2020'>
                        Wow! This is incredible, you guys have somehow surpassed both our original and our stretch goals!
                        How did that happen so quickly! We're going to be able to make High Heat the way we want to make
                        it! The next things we're looking forward to are potentially hiring some seasoned crewmembers
                        that could help us with the little invisible things that just make the film shine.
                        Also, the more money we get the more film festivals we will get to send this film out to!
                        We want the world to see this movie, and you're helping make that happen! Seriously, this is so
                        amazing to see!
                        <br/><br/>
                        Thank you all so very very much for supporting us!
                    </Story>
                    <Story title='Unbelievable! We hit our goal in just 8 hours!' date='March 5, 2020'>
                        Thank you so much! In just 8 hours, we hit our primary goal! This is amazing! Massive thanks
                        to everyone who has donated, you are all amazing and are making this movie happen!
                        Crowdfunding is amazing! Now that we have hit our first goal, we're able to start working
                        toward our stretch goal of $800 and beyond! Any future donations will be incredibly helpful in
                        helping us lock down accommodations for our actors, better-looking props, better food for our
                        cast and crew, and will even help us with film festival submissions! This movie is really
                        happening and we are so unbelievably psyched!
                        <br/><br/>
                        Thank you so much again!

                    </Story>
                    <Story title='High Heat Kickstarter!' date='March 5, 2020'>
                        I've just started raising money for the final film that I'm going to make during my time at
                        Connecticut College with my great friend and film partner, Jack Pacilio. We're making a film
                        called High Heat that follows a fry cook at a hot dog shop as it is invaded by some seriously
                        evil forces. There's time travel, crazy characters, and even live music! It's going to be a
                        really fun and unique project, but we need your help!
                        <br/><br/>
                        Please take a look at the Kickstarter, and if you're able to donate anything at all it would be
                        seriously appreciated. If you can't donate, please consider sharing the link!
                        <br/><br/>
                        Kickstarter is an all-or-nothing deal, so if we don't hit our goal of $500 then we won't get
                        any of it! So please help us out in any way you can!
                        <br/><br/>
                        Thank you so much, we're really psyched to be making this movie!
                        <br/><br/>
                        <a href='https://www.kickstarter.com/projects/zackbermack/high-heat-a-short-film/posts/2776843'>
                            https://www.kickstarter.com/projects/zackbermack/high-heat-a-short-film/posts/2776843
                        </a>

                    </Story>
                </Container>
            </Segment>
        );
    }
}

class Story extends Component{
    render(){

        return(
            <Container>
                <Header
                    className='ui color1'
                    as='h2'>
                    {this.props.title}
                    <Header
                        className='ui color1'
                        as ='h3' content={this.props.date}/>
                </Header>
                <p className='newssize'>
                    {this.props.children}
                    <br/><br/><br/><br/>
                </p>
            </Container>
        );
    }
}

export default News;