import React, {Component} from 'react';
import {Container, Icon, Segment, Image, Grid} from 'semantic-ui-react';
import '.././styles.css';

class Resume extends Component{
    render(){
        return (
            <Segment  inverted basic>
                <Container className="whoop">
                    <h1>Resume</h1>
                    <p className='newssize'>
                        <Grid className='ui spaceabove' centered columns={2} container>
                            <Grid.Row>
                                <Grid.Column>
                                    <Image className='shadowimage' centered src={require('../Images/lighting.JPG')}/>
                                </Grid.Column>
                                <Grid.Column>
                                    <Image className='shadowimage' centered src={require('../Images/apron.JPG')}/>
                                </Grid.Column>
                            </Grid.Row>
                        </Grid>
                        <Icon className='ui spaceabove' name='file alternate'/>
                        <a href={require('../Documents/BermackZackResume.pdf')} download>Zack Bermack Resume</a>
                    </p>
                </Container>
            </Segment>


        );
    }
}

export default Resume;