import React, {Component} from "react";
import {Container, Segment, Grid, Icon} from "semantic-ui-react";

class FootMenu extends Component{
    render(){
        return(
        <Segment inverted basic className="ui color1 segment">
            <Container>
                <Grid divided inverted stackable container columns='5' verticalAlign='middle'>
                    <Grid.Row centered>
                        <Grid.Column textAlign='center' verticalAlign='middle'>
                            <Icon name="linkedin"/>
                            <a href={"https://www.linkedin.com/in/zachary-bermack-554400176"}>Zack Bermack</a>
                        </Grid.Column>
                        <Grid.Column textAlign='center' verticalAlign='middle'>
                            <Icon name="mail"/>
                            <a href={"mailto:zbermack@conncoll.edu"}>zbermack@conncoll.edu</a>
                        </Grid.Column>
                        <Grid.Column  textAlign='center' >
                            <Icon name="imdb"/>
                            <a href={"https://www.imdb.com/name/nm11405214/"}>Zack Bermack</a>
                        </Grid.Column>
                        <Grid.Column  textAlign='center'>
                            <Icon name="phone"/>
                            <span className='phonenum'>
                                973.303.7339
                            </span>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
            </Container>
        </Segment>
        );
    }
}

export default FootMenu;